import * as THREE from 'three';
import waterVertexShader from '../shaders/water.vert';
import waterFragmentShader from '../shaders/water.frag';

export class Water extends THREE.Mesh {
  constructor({ environmentMap, resolution = 512 }) {
    const geometry = new THREE.PlaneGeometry(2, 2, resolution, resolution);

    const material = new THREE.ShaderMaterial({
      vertexShader: waterVertexShader,
      fragmentShader: waterFragmentShader,
      uniforms: {
        uTime: { value: 0 },
        uOpacity: { value: 1.0 },

        uWavesAmplitude: { value: 0.004 },
        uWavesFrequency: { value: 5.0 },
        uWavesPersistence: { value: 0.6 },
        uWavesLacunarity: { value: 2. },
        uWavesIterations: { value: 4 },
        uWavesSpeed: { value: 0.2 },

        uTroughColor: { value: new THREE.Color(0.00, 0.66, 1.00) },
        uSurfaceColor: { value: new THREE.Color(0.00, 0.19, 0.39) },
        uPeakColor: { value: new THREE.Color(0.00, 0.00, 0.00) },
        uPeakThreshold: { value: 0.05 },
        uPeakTransition: { value: 0.05 },
        uTroughThreshold: { value: -0.05 },
        uTroughTransition: { value: 0.05 },

        uFresnelScale: { value: 0.5 },
        uFresnelPower: { value: 2.0 },

        uEnvironmentMap: { value: environmentMap },

        uLightDirection: { value: new THREE.Vector3(0, -1, 0) },
        uLightColor: { value: new THREE.Color(1.0, 1.0, 0.95) },
      },
      transparent: true
    });

    super(geometry, material);
    this.rotation.x = -Math.PI / 2;
  }

  update(elapsedTime) {
    this.material.uniforms.uTime.value = elapsedTime;
  }
}
