// main.js
import * as THREE from 'three';
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls';
import { Sky } from 'three/examples/jsm/objects/Sky.js';
import { Water } from './objects/Water';
import { Ground } from './objects/Ground';
import { setupUI } from './ui';

const clock = new THREE.Clock();
const scene = new THREE.Scene();
const camera = new THREE.PerspectiveCamera(60, window.innerWidth / window.innerHeight, 0.001, 100);
const renderer = new THREE.WebGLRenderer({ antialias: true });
renderer.setSize(window.innerWidth, window.innerHeight);
renderer.setPixelRatio(devicePixelRatio);
document.body.appendChild(renderer.domElement);

const sky = new Sky();
sky.scale.setScalar(1000);
scene.add(sky);

const sun = new THREE.Vector3();
const skyUniforms = sky.material.uniforms;

const skyParams = {
  turbidity: 10,
  rayleigh: 2,
  mieCoefficient: 0.005,
  mieDirectionalG: 0.8,
  elevation: 60,
  azimuth: 180,
  exposure: 1,
  atualizarCeu: atualizarCeu
};

function atualizarCeu(elevation, azimuth) {
  const phi = THREE.MathUtils.degToRad(90 - elevation);
  const theta = THREE.MathUtils.degToRad(azimuth);
  sun.setFromSphericalCoords(1, phi, theta);
  skyUniforms['sunPosition'].value.copy(sun);
  renderer.toneMappingExposure = skyParams.exposure;
}

scene.add(new THREE.AmbientLight(0xffffff, 0.5));
camera.position.set(-0.003, 0.048, -0.137);

const directionalLight = new THREE.DirectionalLight(0xffffff, 1);
directionalLight.position.set(0, 10, 0);
scene.add(directionalLight);
window.directionalLight = directionalLight;

const controls = new OrbitControls(camera, renderer.domElement);
controls.enableDamping = true;
controls.enabled = false; // OrbitControls desativado inicialmente

const textureLoader = new THREE.TextureLoader();
const poolTexture = textureLoader.load('/threejs-water-shader/ocean_floor.png');

const cubeRenderTarget = new THREE.WebGLCubeRenderTarget(256, {
  format: THREE.RGBAFormat,
  generateMipmaps: true,
  minFilter: THREE.LinearMipmapLinearFilter
});
const cubeCamera = new THREE.CubeCamera(0.1, 1000, cubeRenderTarget);
scene.add(cubeCamera);

const waterResolution = { size: 270, scale:5};
const water = new Water({
  environmentMap: cubeRenderTarget.texture,
  resolution: waterResolution.size
});

// Adicionar o uniform `uOceanTransition` ao material da gua
water.material.uniforms.uOceanTransition = { value: 0.0 }; // Inicializa com 0.0 (Calmo)
scene.add(water);

const ground = new Ground({ texture: poolTexture });
scene.add(ground);

// Criao da barra de progresso (mantenha apenas uma vez)
const progressBarContainer = document.createElement('div');
progressBarContainer.style.position = 'absolute';
progressBarContainer.style.bottom = '20px';
progressBarContainer.style.left = '50%';
progressBarContainer.style.transform = 'translateX(-50%)';
progressBarContainer.style.width = '80%';
progressBarContainer.style.height = '10px';
progressBarContainer.style.background = 'rgba(255, 255, 255, 0.2)';
progressBarContainer.style.borderRadius = '5px';
progressBarContainer.style.overflow = 'hidden';

const progressBar = document.createElement('div');
progressBar.style.width = '0%';
progressBar.style.height = '100%';
progressBar.style.background = 'rgba(0, 150, 255, 0.8)';
progressBar.style.transition = 'width 0.1s linear';

progressBarContainer.appendChild(progressBar);
document.body.appendChild(progressBarContainer);

// Criao do elemento de texto para o progresso (mantenha apenas uma vez)
const progressText = document.createElement('div');
progressText.style.position = 'absolute';
progressText.style.bottom = '20px';
progressText.style.left = '50%';
progressText.style.transform = 'translateX(-50%)';
progressText.style.color = 'white';
progressText.style.fontFamily = 'Arial, sans-serif';
progressText.style.fontSize = '16px';
progressText.style.background = 'rgba(0, 0, 0, 0.5)';
progressText.style.padding = '5px 10px';
progressText.style.borderRadius = '5px';
progressText.textContent = 'Progresso: 0%';
document.body.appendChild(progressText);

// Inicializar variveis globais
let currentAnimation = null; // Varivel para rastrear a animao atual
let currentProgress = 0; // Inicializa o progresso atual em 0
const floatingCubes = []; // Array para armazenar os cubos flutuantes

// Criar cubos flutuantes
for (let i = 0; i < 10; i++) { // Nmero de cubos
  const cubeGeometry = new THREE.BoxGeometry(0.1, 0.1, 0.1); // Tamanho do cubo
  const cubeMaterial = new THREE.MeshStandardMaterial({ color: 0xffa500, transparent: true, opacity: 0 });
  const cube = new THREE.Mesh(cubeGeometry, cubeMaterial);

  // Definir posio inicial dos cubos
  cube.position.set(Math.random() * 2 - 1, -1, Math.random() * 2 - 1); // Posio aleatria
  cube.userData.fixedX = cube.position.x;
  cube.userData.fixedZ = cube.position.z;

  cube.visible = false; // Invisvel no incio
  floatingCubes.push(cube); // Adicionar ao array
  scene.add(cube); // Adicionar  cena
}

const calmAudio = new Audio('./oceano.mp3'); // Caminho correto para o som calmo
calmAudio.loop = true;
calmAudio.volume = 0.7;
let calmAudioPlaying = false;

const stormAudio = new Audio('./vozes.mp3');
stormAudio.loop = true;
stormAudio.volume = 0;
let stormAudioPlaying = false;

// Garante que o som calmo est sempre a tocar
if (!calmAudioPlaying) {
    calmAudio.play();
    calmAudioPlaying = true;
}

const oceanState = { mode: 'Calmo' }; // Estado inicial  "Calmo"

// Chamar applyOceanState aps a criao da barra de progresso
applyOceanState(oceanState.mode); // Aplica o estado inicial

// Funo para atualizar os controladores (UI)
function atualizarControladores() {
  if (window.pane) {
    // Atualiza os valores dos controladores para refletir o estado atual
    const params = window.pane.params;
    if (params) {
      params.amp = water.material.uniforms.uWavesAmplitude.value;
      params.freq = water.material.uniforms.uWavesFrequency.value;
      params.pers = water.material.uniforms.uWavesPersistence.value;
      params.lacu = water.material.uniforms.uWavesLacunarity.value;
      params.iter = water.material.uniforms.uWavesIterations.value;
      params.speed = water.material.uniforms.uWavesSpeed.value;
      params.turb = skyUniforms['turbidity'].value;
      params.ray = skyUniforms['rayleigh'].value;
      params.mie = skyUniforms['mieCoefficient'].value;
      params.mieG = skyUniforms['mieDirectionalG'].value;
      params.elev = skyParams.elevation;
      params.azi = skyParams.azimuth;
    }
    window.pane.refresh(); // Atualiza a UI
  }
}

function applyOceanState(mode) {
  if (currentAnimation) {
    cancelAnimationFrame(currentAnimation); // Cancela a animao anterior
    currentAnimation = null;
  }

  // Atualiza o estado global para o novo modo
  oceanState.mode = mode;

  const tempo = mode === 'Tempestade' ? 30 : 10; // Durao em segundos
  const passos = 60 * tempo;
  let passoAtual = 0;

  const ini = {
    amp: water.material.uniforms.uWavesAmplitude.value || 0.004,
    freq: water.material.uniforms.uWavesFrequency.value || 5.0,
    pers: water.material.uniforms.uWavesPersistence.value || 0.6,
    lacu: water.material.uniforms.uWavesLacunarity.value || 2.2,
    iter: water.material.uniforms.uWavesIterations.value || 4,
    speed: water.material.uniforms.uWavesSpeed.value || 0.2,
    trough: water.material.uniforms.uTroughColor.value.clone(),
    surface: water.material.uniforms.uSurfaceColor.value.clone(),
    peak: water.material.uniforms.uPeakColor.value.clone(),
    fresnelS: water.material.uniforms.uFresnelScale.value,
    fresnelP: water.material.uniforms.uFresnelPower.value,
    turb: skyUniforms['turbidity'].value,
    ray: skyUniforms['rayleigh'].value,
    mie: skyUniforms['mieCoefficient'].value,
    mieG: skyUniforms['mieDirectionalG'].value,
    elev: skyParams.elevation,
    azi: skyParams.azimuth
  };

  const alvo = mode === 'Tempestade' ? {
    amp: 0.037,
    freq: 3.54,
    pers: 0.3,
    lacu: 2.18,
    iter: 3,
    speed: 0.2,
    trough: new THREE.Color(1.0, 0.0, 0.0),
    surface: new THREE.Color(1.0, 0.0, 0.0),
    peak: new THREE.Color(1.0, 0.0, 0.0),
    fresnelS: 0.39,
    fresnelP: 3.0,
    turb: 20,
    ray: 0.2,
    mie: 0.1,
    mieG: 0.99,
    elev: 5,
    azi: 90
  } : {
    amp: 0.004,
    freq: 5.0,
    pers: 0.6,
    lacu: 2.2,
    iter: 4,
    speed: 0.2,
    trough: new THREE.Color(0.00, 0.66, 1.00),
    surface: new THREE.Color(0.00, 0.19, 0.39),
    peak: new THREE.Color(0.00, 0.00, 0.0),
    fresnelS: 0.8,
    fresnelP: 1.6,
    turb: 3,
    ray: 2.8,
    mie: 0.001,
    mieG: 0.3,
    elev: 60,
    azi: 180
  };

  const animar = () => {
    if (oceanState.mode !== mode) return;

    passoAtual++;
    const t = passoAtual / passos;
    if (t > 1) {
      atualizarControladores();
      currentProgress = mode === 'Tempestade' ? 100 : 0;
      progressText.textContent = `Progresso: ${currentProgress}%`;

      // Finaliza a visibilidade dos cubos com base no progresso final
      floatingCubes.forEach((cube, index) => {
        const delay = index * 0.1; // Atraso para cada cubo
        if (mode === 'Tempestade') {
          setTimeout(() => {
            cube.visible = true;
            cube.material.opacity = 1; // Totalmente visvel
          }, delay * 1000);
        } else {
          setTimeout(() => {
            cube.material.opacity = 0; // Totalmente invisvel
            cube.visible = false;
          }, delay * 1000);
        }
      });

      return;
    }

    // Atualiza o progresso
    currentProgress = mode === 'Tempestade' ? t * 100 : (1 - t) * 100;
    progressText.textContent = `Progresso: ${currentProgress.toFixed(0)}%`;
    progressBar.style.width = `${currentProgress}%`;

    // --- CONTROLO DE UDIO TEMPESTADE (ajustado para s tocar aps 20%) ---
    const minVol = 0.0;
    const maxVol = 1.0;
    const minPitch = 1.0;
    const maxPitch = 1.15;
    const animProgress = mode === 'Tempestade' ? t : 1 - t;

    // S comea a tocar stormAudio aps 20% de progresso
    let stormFade = 0;
    if (animProgress > 0.2) {
        stormFade = (animProgress - 0.2) / 0.8; // Normaliza de 0 a 1 entre 20% e 100%
        stormFade = Math.min(Math.max(stormFade, 0), 1);
    }

    stormAudio.volume = minVol + (maxVol - minVol) * stormFade;
    stormAudio.playbackRate = minPitch + (maxPitch - minPitch) * stormFade;

    if (stormAudio.volume > 0) {
        if (stormAudio.paused) {
            stormAudio.play();
        }
    } else {
        if (!stormAudio.paused) {
            stormAudio.pause();
            stormAudio.currentTime = 0;
        }
    }

    calmAudio.volume = 0.7;
    calmAudio.playbackRate = 1.0;
    // --- FIM CONTROLO DE UDIO ---

    // Controla a visibilidade e o movimento dos cubos
    floatingCubes.forEach((cube, index) => {
      const delay = index * 0.1; // Atraso para cada cubo
      if (currentProgress >= 40 && mode === 'Tempestade') {
        setTimeout(() => {
          cube.visible = true;
          cube.material.opacity = Math.min((currentProgress - 40) / 10, 1); // Fade-in
        }, delay * 1000);
      } else if (currentProgress <= 40 && mode === 'Calmo') {
        setTimeout(() => {
          cube.material.opacity = Math.max((currentProgress - 40) / 10, 0); // Fade-out
          if (cube.material.opacity <= 0) {
            cube.visible = false;
          }
        }, delay * 1000);
      }
    });

    // Interpolao dos valores
    water.material.uniforms.uWavesAmplitude.value = THREE.MathUtils.lerp(ini.amp, alvo.amp, t);
    water.material.uniforms.uWavesFrequency.value = THREE.MathUtils.lerp(ini.freq, alvo.freq, t);
    water.material.uniforms.uWavesPersistence.value = THREE.MathUtils.lerp(ini.pers, alvo.pers, t);
    water.material.uniforms.uWavesLacunarity.value = THREE.MathUtils.lerp(ini.lacu, alvo.lacu, t);
    water.material.uniforms.uWavesIterations.value = THREE.MathUtils.lerp(ini.iter, alvo.iter, t);
    water.material.uniforms.uWavesSpeed.value = THREE.MathUtils.lerp(ini.speed, alvo.speed, t);

    water.material.uniforms.uTroughColor.value.lerpColors(ini.trough, alvo.trough, t);
    water.material.uniforms.uSurfaceColor.value.lerpColors(ini.surface, alvo.surface, t);
    water.material.uniforms.uPeakColor.value.lerpColors(ini.peak, alvo.peak, t);

    water.material.uniforms.uFresnelScale.value = THREE.MathUtils.lerp(ini.fresnelS, alvo.fresnelS, t);
    water.material.uniforms.uFresnelPower.value = THREE.MathUtils.lerp(ini.fresnelP, alvo.fresnelP, t);
    skyUniforms['turbidity'].value = THREE.MathUtils.lerp(ini.turb, alvo.turb, t);
    skyUniforms['rayleigh'].value = THREE.MathUtils.lerp(ini.ray, alvo.ray, t);
    skyUniforms['mieCoefficient'].value = THREE.MathUtils.lerp(ini.mie, alvo.mie, t);
    skyUniforms['mieDirectionalG'].value = THREE.MathUtils.lerp(ini.mieG, alvo.mieG, t);

    skyParams.elevation = THREE.MathUtils.lerp(ini.elev, alvo.elev, t);
    skyParams.azimuth = THREE.MathUtils.lerp(ini.azi, alvo.azi, t);
    atualizarCeu(skyParams.elevation, skyParams.azimuth);

    // Atualizar a transio do contorno (preto -> laranja a partir de 80%)
    if (water.material.uniforms.uOceanTransition) {
      water.material.uniforms.uOceanTransition.value = mode === 'Tempestade' ? t : 1 - t; // Atualiza o valor de transio
    } else {
      console.error('Uniform uOceanTransition no est definido!');
    }

    currentAnimation = requestAnimationFrame(animar);
  };

  animar();
}

// Esconder controladores no arranque
window.addEventListener('load', () => {
  const paneInicial = document.querySelector('.tp-dfwv'); // Classe padro do Tweakpane
  if (paneInicial) {
    paneInicial.style.display = 'none'; // Esconde o elemento inicialmente
  }
});

// Alternar visibilidade dos controladores ao pressionar a tecla T
window.addEventListener('keydown', (event) => {
  if (event.key.toLowerCase() === 't') {
    const pane = document.querySelector('.tp-dfwv'); // Classe padro do Tweakpane
    if (pane) {
      pane.style.display = pane.style.display === 'none' ? 'block' : 'none';
    }
  }
});

// Alternar OrbitControls ao pressionar a tecla R
let orbitControlsEnabled = false; // Estado inicial desativado
window.addEventListener('keydown', (event) => {
  if (event.key.toLowerCase() === 'r') {
    orbitControlsEnabled = !orbitControlsEnabled;
    controls.enabled = orbitControlsEnabled; // Ativar ou desativar o OrbitControls
  }
});

// Alternar estados "Calmo" e "Tempestade" ao pressionar as teclas 1 e 2
window.addEventListener('keydown', (event) => {
  if (event.key === '1') {
    applyOceanState('Calmo'); // Alterna para o estado "Calmo"
  } else if (event.key === '2') {
    applyOceanState('Tempestade'); // Alterna para o estado "Tempestade"
  }
});

window.addEventListener('resize', () => {
  camera.aspect = window.innerWidth / window.innerHeight;
  camera.updateProjectionMatrix();
  renderer.setSize(window.innerWidth, window.innerHeight);
});

const coordsDiv = document.createElement('div');
coordsDiv.style.position = 'absolute';
coordsDiv.style.bottom = '10px';
coordsDiv.style.left = '10px';
coordsDiv.style.color = 'white';
coordsDiv.style.background = 'rgba(0,0,0,0.5)';
coordsDiv.style.padding = '5px';
coordsDiv.style.fontFamily = 'monospace';
document.body.appendChild(coordsDiv);

const pane = setupUI({ waterResolution, water, oceanState, applyOceanState });

function calcularAlturaEGradiente(x, z, time) {
  const amplitude = water.material.uniforms.uWavesAmplitude.value;
  const frequency = water.material.uniforms.uWavesFrequency.value;
  const persistence = water.material.uniforms.uWavesPersistence.value;
  const lacunarity = water.material.uniforms.uWavesLacunarity.value;
  const iterations = water.material.uniforms.uWavesIterations.value;
  const speed = water.material.uniforms.uWavesSpeed.value;

  let altura = 0;
  let gradienteX = 0;
  let gradienteZ = 0;

  let amplitudeAtual = amplitude;
  let frequencyAtual = frequency;

  for (let i = 0; i < iterations; i++) {
    const nx = x * frequencyAtual + time * speed;
    const nz = z * frequencyAtual + time * speed;

    const onda = Math.sin(nx + nz);
    altura += amplitudeAtual * onda;

    const derivada = Math.cos(nx + nz);
    gradienteX += amplitudeAtual * derivada * frequencyAtual;
    gradienteZ += amplitudeAtual * derivada * frequencyAtual;

    amplitudeAtual *= persistence;
    frequencyAtual *= lacunarity;
  }

  return { altura, gradienteX, gradienteZ };
}

function calcularAlturaOnda(x, z, time) {
  const amplitude = water.material.uniforms.uWavesAmplitude.value; // Amplitude das ondas
  const frequency = water.material.uniforms.uWavesFrequency.value; // Frequncia das ondas
  const speed = water.material.uniforms.uWavesSpeed.value; // Velocidade das ondas

  // Calcula a altura da onda com base na posio (x, z) e no tempo
  return Math.sin((x + z) * frequency + time * speed) * amplitude;
}

function atualizarVerticesDoCubo(cube, time) {
  const positionAttribute = cube.geometry.attributes.position;

  for (let i = 0; i < positionAttribute.count; i++) {
    const x = positionAttribute.getX(i) + cube.position.x; // Posio X global do vrtice
    const z = positionAttribute.getZ(i) + cube.position.z; // Posio Z global do vrtice

    // Calcula a altura da onda para o vrtice
    const waveHeight = calcularAlturaOnda(x, z, time);

    // Atualiza a posio Y do vrtice
    positionAttribute.setY(i, waveHeight);
  }

  positionAttribute.needsUpdate = true; // Marca a geometria como atualizada
}

function atualizarCubos(elapsedTime) {
  floatingCubes.forEach((cube) => {
    if (cube.visible) {
      const x = cube.userData.fixedX;
      const z = cube.userData.fixedZ;

      // Calcula a altura e os gradientes da onda na posio do cubo
      const { altura, gradienteX, gradienteZ } = calcularAlturaEGradiente(x, z, elapsedTime);

      // Atualiza a posio Y do cubo para alinhar o centro com a linha d'gua
      cube.position.y = altura;

      // Atualiza a rotao do cubo para simular o balano
      cube.rotation.x = -gradienteZ * 0.5; // Inclinao em relao ao eixo Z
      cube.rotation.z = gradienteX * 0.5; // Inclinao em relao ao eixo X
    }
  });
}

function animate() {
  const elapsedTime = clock.getElapsedTime();

  // Atualizar a gua e o terreno
  water.update(elapsedTime);
  ground.update(elapsedTime);
  controls.update();

  // Atualizar a posio e a rotao dos cubos para seguir as ondas
  atualizarCubos(elapsedTime);

  // Capturar reflexo do cu com CubeCamera
  water.visible = false;
  cubeCamera.position.copy(water.position);
  cubeCamera.update(renderer, scene);
  water.visible = true;

  // Atualizar uniforms de luz para gua
  water.material.uniforms.uLightDirection.value.copy(sun).normalize();
  water.material.uniforms.uLightColor.value.copy(directionalLight.color);

  // Atualizar texto de coordenadas da cmera
  const pos = camera.position;
  coordsDiv.textContent = `Cmara: x=${pos.x.toFixed(3)} y=${pos.y.toFixed(3)} z=${pos.z.toFixed(3)}`;

  // Renderizar a cena
  renderer.render(scene, camera);
  requestAnimationFrame(animate);
}

animate();







