// ui.js (sem ground)
import { Pane } from 'tweakpane';
import * as THREE from 'three';

export function setupUI({ waterResolution, water, oceanState, applyOceanState }) {
  const pane = new Pane();
  const paneElement = pane.element;
  paneElement.style.position = 'absolute';
  paneElement.style.top = '0';
  paneElement.style.right = '0';
  paneElement.style.zIndex = '100';
  paneElement.id = 'tweakpane-ui';

  const pastaAgua = pane.addFolder({ title: 'gua' });

  const pastaGeometria = pastaAgua.addFolder({ title: 'Geometria' });
  pastaGeometria.addBinding(waterResolution, 'size', {
    min: 2, max: 1024, step: 2, label: 'Resoluo da Malha'
  }).on('change', () => {
    const novaGeometria = new THREE.PlaneGeometry(2, 2, waterResolution.size, waterResolution.size);
    water.geometry.dispose();
    water.geometry = novaGeometria;
  });

  const pastaOndas = pastaAgua.addFolder({ title: 'Ondas' });
  pastaOndas.addBinding(water.material.uniforms.uWavesAmplitude, 'value', { min: 0, max: 0.1, label: 'Altura das Ondas' });
  pastaOndas.addBinding(water.material.uniforms.uWavesFrequency, 'value', { min: 0.1, max: 10, label: 'Frequncia das Ondas' });
  pastaOndas.addBinding(water.material.uniforms.uWavesPersistence, 'value', { min: 0, max: 1, label: 'Persistncia do Rudo' });
  pastaOndas.addBinding(water.material.uniforms.uWavesLacunarity, 'value', { min: 0, max: 3, label: 'Lacunaridade do Rudo' });
  pastaOndas.addBinding(water.material.uniforms.uWavesIterations, 'value', { min: 1, max: 10, step: 1, label: 'Nvel de Detalhe' });
  pastaOndas.addBinding(water.material.uniforms.uWavesSpeed, 'value', { min: 0, max: 1, label: 'Velocidade das Ondas' });

  const pastaCores = pastaAgua.addFolder({ title: 'Cores da gua' });
  pastaCores.addBinding(water.material.uniforms.uOpacity, 'value', { min: 0, max: 1, step: 0.01, label: 'Transparncia' });
  pastaCores.addBinding(water.material.uniforms.uTroughColor, 'value', { label: 'Cor dos Vales', view: 'color', color: { type: 'float' } });
  pastaCores.addBinding(water.material.uniforms.uSurfaceColor, 'value', { label: 'Cor da Superfcie', view: 'color', color: { type: 'float' } });
  pastaCores.addBinding(water.material.uniforms.uPeakColor, 'value', { label: 'Cor das Cristas', view: 'color', color: { type: 'float' } });
  pastaCores.addBinding(water.material.uniforms.uPeakThreshold, 'value', { min: 0, max: 0.5, label: 'Limiar da Crista' });
  pastaCores.addBinding(water.material.uniforms.uPeakTransition, 'value', { min: 0, max: 0.5, label: 'Suavidade da Crista' });
  pastaCores.addBinding(water.material.uniforms.uTroughThreshold, 'value', { min: -0.5, max: 0, label: 'Limiar do Vale' });
  pastaCores.addBinding(water.material.uniforms.uTroughTransition, 'value', { min: 0, max: 0.5, label: 'Suavidade do Vale' });

  const pastaFresnel = pastaAgua.addFolder({ title: 'Reflexos (Fresnel)' });
  pastaFresnel.addBinding(water.material.uniforms.uFresnelScale, 'value', { min: 0, max: 1, label: 'Intensidade do Reflexo' });
  pastaFresnel.addBinding(water.material.uniforms.uFresnelPower, 'value', { min: 0, max: 3, label: 'Curvatura do Reflexo' });

  const pastaEstado = pane.addFolder({ title: 'Estado do Mar' });
  pastaEstado.addBinding(oceanState, 'mode', {
    options: {
      ' Calmo': 'Calmo',
      ' Tempestade': 'Tempestade',
      ' Transio': 'Transio'
    },
    label: 'Estado'
  }).on('change', ({ value }) => {
    applyOceanState(value);
  });

  const pastaCeu = pane.addFolder({ title: 'Cu' });
  const skyUniforms = water.material.userData?.skyUniforms;
  const skyParams = water.material.userData?.skyParams;

  if (skyUniforms && skyParams) {
    pastaCeu.addBinding(skyParams, 'turbidity', { min: 0, max: 20, label: 'Turbidez' }).on('change', v => {
      skyUniforms['turbidity'].value = v.value;
    });
    pastaCeu.addBinding(skyParams, 'rayleigh', { min: 0, max: 4, label: 'Disperso (Rayleigh)' }).on('change', v => {
      skyUniforms['rayleigh'].value = v.value;
    });
    pastaCeu.addBinding(skyParams, 'mieCoefficient', { min: 0, max: 0.1, label: 'Coef. Mie' }).on('change', v => {
      skyUniforms['mieCoefficient'].value = v.value;
    });
    pastaCeu.addBinding(skyParams, 'mieDirectionalG', { min: 0, max: 1, label: 'Direcionalidade Mie' }).on('change', v => {
      skyUniforms['mieDirectionalG'].value = v.value;
    });
    pastaCeu.addBinding(skyParams, 'elevation', { min: 0, max: 90, label: 'Elevao Solar' }).on('change', v => {
      if (typeof skyParams.atualizarCeu === 'function') skyParams.atualizarCeu(v.value, skyParams.azimuth);
    });
    pastaCeu.addBinding(skyParams, 'azimuth', { min: -180, max: 180, label: 'Azimute Solar' }).on('change', v => {
      if (typeof skyParams.atualizarCeu === 'function') skyParams.atualizarCeu(skyParams.elevation, v.value);
    });
  }

  return pane;
}

